@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@400;500;600;700;800&family=Inter:wght@300;400;500;600&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    margin: 0;
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #FAFAF9;
    color: #1C1917;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Manrope', sans-serif;
    letter-spacing: -0.02em;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

/* Custom scrollbar */
::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

::-webkit-scrollbar-track {
    background: #F5F5F4;
}

::-webkit-scrollbar-thumb {
    background: #D4A373;
    border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
    background: #1B4D3E;
}

/* Selection */
::selection {
    background-color: #1B4D3E;
    color: white;
}

@layer base {
    :root {
        --background: 40 20% 98%;
        --foreground: 24 10% 10%;
        --card: 0 0% 100%;
        --card-foreground: 24 10% 10%;
        --popover: 0 0% 100%;
        --popover-foreground: 24 10% 10%;
        --primary: 160 48% 20%;
        --primary-foreground: 0 0% 100%;
        --secondary: 30 47% 64%;
        --secondary-foreground: 24 10% 10%;
        --muted: 40 20% 96%;
        --muted-foreground: 24 6% 45%;
        --accent: 27 83% 60%;
        --accent-foreground: 24 10% 10%;
        --destructive: 0 84% 60%;
        --destructive-foreground: 0 0% 98%;
        --border: 30 10% 90%;
        --input: 30 10% 90%;
        --ring: 160 48% 20%;
        --radius: 0.75rem;
        --chart-1: 160 48% 20%;
        --chart-2: 30 47% 64%;
        --chart-3: 27 83% 60%;
        --chart-4: 40 20% 50%;
        --chart-5: 24 10% 30%;
    }
}

@layer base {
    * {
        @apply border-border;
    }
    body {
        @apply bg-background text-foreground;
    }
}

@layer base {
    [data-debug-wrapper="true"] {
        display: contents !important;
    }
}

/* Animation classes */
@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(30px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes float {
    0%, 100% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-10px);
    }
}

@keyframes pulse-glow {
    0%, 100% {
        box-shadow: 0 0 20px rgba(27, 77, 62, 0.3);
    }
    50% {
        box-shadow: 0 0 40px rgba(27, 77, 62, 0.5);
    }
}

.animate-fade-in-up {
    animation: fadeInUp 0.6s ease-out forwards;
}

.animate-float {
    animation: float 3s ease-in-out infinite;
}

.animate-pulse-glow {
    animation: pulse-glow 2s ease-in-out infinite;
}

/* Glass effect */
.glass {
    background: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(16px);
    -webkit-backdrop-filter: blur(16px);
}

/* Grain texture overlay */
.grain::before {
    content: '';
    position: absolute;
    inset: 0;
    background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 256 256' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noise'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.65' numOctaves='3' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noise)'/%3E%3C/svg%3E");
    opacity: 0.03;
    pointer-events: none;
    z-index: 1;
}

/* Button styles */
.btn-primary {
    @apply bg-[#1B4D3E] text-white hover:bg-[#143A2F] rounded-full px-8 py-3 font-semibold shadow-lg transition-all duration-300;
}

.btn-primary:hover {
    transform: translateY(-2px);
    box-shadow: 0 10px 25px rgba(27, 77, 62, 0.3);
}

.btn-secondary {
    @apply bg-[#D4A373] text-[#1C1917] hover:bg-[#C39262] rounded-full px-6 py-2 font-medium transition-colors duration-200;
}

.btn-outline {
    @apply border-2 border-[#1B4D3E] text-[#1B4D3E] hover:bg-[#1B4D3E] hover:text-white rounded-full px-6 py-2 font-medium transition-colors duration-200;
}

/* Card styles */
.card-hover {
    @apply bg-white rounded-2xl border border-stone-100 shadow-sm transition-shadow duration-300;
}

.card-hover:hover {
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.08);
}

/* Input styles */
.input-style {
    @apply bg-white border-stone-200 focus:border-[#1B4D3E] focus:ring-2 focus:ring-[#1B4D3E]/20 rounded-lg h-11 px-4 transition-all duration-200;
}

/* Admin sidebar styles */
.admin-sidebar {
    @apply bg-[#1B4D3E] text-white min-h-screen;
}

.admin-nav-link {
    @apply flex items-center gap-3 px-4 py-3 rounded-lg text-white/80 hover:text-white hover:bg-white/10 transition-colors duration-200;
}

.admin-nav-link.active {
    @apply bg-white/20 text-white;
}
