/* Main App Styles - Dreamfield Agrisolutions */

/* Hero section gradient overlay */
.hero-gradient {
    background: linear-gradient(135deg, rgba(27, 77, 62, 0.85) 0%, rgba(27, 77, 62, 0.6) 100%);
}

/* Stats card hover effect */
.stats-card {
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.stats-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 12px 30px rgba(27, 77, 62, 0.15);
}

/* Product card image zoom */
.product-card-image {
    transition: transform 0.4s ease;
}

.product-card:hover .product-card-image {
    transform: scale(1.05);
}

/* Navigation link underline effect */
.nav-link {
    position: relative;
}

.nav-link::after {
    content: '';
    position: absolute;
    bottom: -4px;
    left: 0;
    width: 0;
    height: 2px;
    background-color: #1B4D3E;
    transition: width 0.3s ease;
}

.nav-link:hover::after,
.nav-link.active::after {
    width: 100%;
}

/* Badge styles */
.badge-success {
    @apply bg-green-100 text-green-800 px-3 py-1 rounded-full text-sm font-medium;
}

.badge-warning {
    @apply bg-amber-100 text-amber-800 px-3 py-1 rounded-full text-sm font-medium;
}

.badge-info {
    @apply bg-blue-100 text-blue-800 px-3 py-1 rounded-full text-sm font-medium;
}

/* Form focus states */
input:focus, textarea:focus, select:focus {
    outline: none;
    border-color: #1B4D3E;
    box-shadow: 0 0 0 3px rgba(27, 77, 62, 0.1);
}

/* Table row hover */
.table-row-hover:hover {
    background-color: #FAFAF9;
}

/* Breadcrumb separator */
.breadcrumb-separator {
    color: #A8A29E;
}

/* Page transition */
.page-transition {
    animation: pageEnter 0.4s ease-out;
}

@keyframes pageEnter {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Loading spinner */
.spinner {
    border: 3px solid #E7E5E4;
    border-top-color: #1B4D3E;
    border-radius: 50%;
    width: 24px;
    height: 24px;
    animation: spin 0.8s linear infinite;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

/* Mobile menu overlay */
.mobile-menu-overlay {
    background: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(4px);
}

/* Tooltip */
.tooltip {
    position: relative;
}

.tooltip::after {
    content: attr(data-tooltip);
    position: absolute;
    bottom: 100%;
    left: 50%;
    transform: translateX(-50%);
    padding: 8px 12px;
    background: #1C1917;
    color: white;
    font-size: 12px;
    border-radius: 6px;
    white-space: nowrap;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.2s, visibility 0.2s;
}

.tooltip:hover::after {
    opacity: 1;
    visibility: visible;
}

/* Custom checkbox */
.custom-checkbox {
    appearance: none;
    width: 20px;
    height: 20px;
    border: 2px solid #D6D3D1;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.2s;
}

.custom-checkbox:checked {
    background-color: #1B4D3E;
    border-color: #1B4D3E;
    background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='white' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M12.207 4.793a1 1 0 010 1.414l-5 5a1 1 0 01-1.414 0l-2-2a1 1 0 011.414-1.414L6.5 9.086l4.293-4.293a1 1 0 011.414 0z'/%3e%3c/svg%3e");
}

/* Skeleton loading */
.skeleton {
    background: linear-gradient(90deg, #F5F5F4 25%, #EBEBEA 50%, #F5F5F4 75%);
    background-size: 200% 100%;
    animation: skeleton-loading 1.5s infinite;
}

@keyframes skeleton-loading {
    0% {
        background-position: 200% 0;
    }
    100% {
        background-position: -200% 0;
    }
}

/* Responsive image container */
.image-container {
    position: relative;
    overflow: hidden;
}

.image-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

/* Form label */
.form-label {
    @apply block text-sm font-medium text-stone-700 mb-2;
}

/* Error text */
.error-text {
    @apply text-red-600 text-sm mt-1;
}

/* Success toast */
.toast-success {
    background: linear-gradient(135deg, #1B4D3E 0%, #2D6A4F 100%);
}

/* Section divider */
.section-divider {
    height: 1px;
    background: linear-gradient(90deg, transparent, #E7E5E4, transparent);
}
